.block {
    &-head {
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 36px;
        line-height: 42px;
        
        padding: 0;
        margin: 0;
    }
    &-main {
        background: #FFFFFF;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05);
        border-radius: 5px;
        
        padding: 18px 16px;
        
        margin-bottom: 6px;
//        border: 1px solid #EDEEF2;
        
    }
    &-right,
    &-left {
        width: 50%;
    }
    
    &-text {
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
                @media (max-width:900px) {
                    font-size: 16px;
                    line-height: 19px;
                }
    }
}