header {
    padding: 15px 0 30px 0;
    background: #FFFFFF;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05);
    .flex {
        align-items: center;
        justify-content: space-between;
    }
}

nav {
    a {
        font-family: $Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        
        color: $grey;
        padding: 8px 0;
        margin: 0 15px;
        border-bottom: 1px solid #fff;
        
        display: inline-block;
        text-decoration: none;
        
        margin-bottom: 15px;
        &:hover,
        &.active {
            border-color: $blue;
        }
    }
}
.tel {
    font-size: 18px;
    font-weight: bold;
    color: $grey;
    
    text-align: right;
    a {
        text-decoration: none;
        color: $grey;
        
        display: block;
        
        transition: $tr;
        &:hover {
            color: $red;
        }
    }
    span {
        font-weight: 100;
    }
}
.header {
    &-top {
        align-items: flex-end;
    }
    &-bottom {
        padding-top: 25px;
    }
}
.logo {
    max-width: 160px;
    margin-bottom: 9px;
    img {
        width: 100%;
    }
}
