
footer {
    background: $blue;
    box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.05);
    
    color: #fff;
    a {
        color: #fff;
        @media (max-width:768px) {
            display: none;
        }
        &.logo {
            margin: 0 auto;
            display: block;
            text-align: center;
            padding-top: 35px;
            img {
                width: auto;
                display: inline-block;
                
            }
        }
        &.tel {
            color: #fff;
            display: none;
            @media (max-width:768px) {
                display: block;
                text-align: center;
            }
        }
    }
    .flex {
        justify-content: center;
        @media (max-width:650px) {
            display: none;
        }
    }
}
.footer {
    &__top {
        display: inline-flex;
        
        background: #FFFFFF;
        box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.05);
        border-radius: 35px;
        
        padding: 15px 0;
        margin: 0 auto;
        transform: translate(0,-50%);
        
        a {
            color: $grey;
            text-decoration: none;
            
            font-style: normal;
            font-weight: bold;
            font-size: 20px;
            line-height: 23px;
            
            padding: 0px 55px;
            
            transition: $tr;
            &:hover {
                color: $red;
            }
            & + a {
                border-left: 1px solid rgba(0, 0, 0, 0.15);
            }
        }
    }
    &__nav {
        text-align: center;
        
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        
        padding: 45px 0 45px 0;

        a {
            display: inline-block;
            text-decoration: none;
            margin: 0 15px 15px 15px;
            transition: $tr;
            @media (max-width:768px) {
                display: none;
            }
            &:hover {
                color: $red; 
            }
        }
    }
}
.copy {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    padding-bottom: 50px;
    span {
        font-weight: 300;
    }
}
