.btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    
    padding: 14px 30px;
    
    border: 2px solid $grey;
    border-radius: 35px;
    
    background: none;
    
    color: $grey;
    font-family: $Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    text-decoration: none;
    
    cursor: pointer;
    
    
    transition: $tr;
    &-red {
        background: $red;
        border-color: $red;
        color: #fff;
        &:hover {
            background: #fff;
            color: $red;
        }
    }
    &-white {
        background: #fff;
        border-color: $red;
        color: $red;
        &:hover {
            background: $red;
            color: #fff;
        }
    }
}