.bread {
    font-family: $Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    color: $lblue;
    
    padding: 28px 0;
    a {
        color: $lblue;
        text-decoration: none;
        margin-right: 17px;
        
        font-weight: 300;
        
        & + a {
            
            &:before {
                content: "\e901";
                font-family: 'icomoon' !important;
                speak: none;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                
                font-size: 10px;
                
                margin-right: 17px;
                
                color: $lblue!important;
            }
        }
        &:last-child {
            color: $blue;
            font-weight: 500;
        }
    }
}