.catalog {
    &-top {
        padding: 5px 0 30px 0;
        justify-content: space-between;
        align-items: center;
    }
    &-left {
        width: 265px;
        min-width: 265px;
        margin-right: 23px;
    }
    &-right {
        
    }
    &__filter {
        
        &-block {
            
        }
        &-head {
            font-family: $Roboto;
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            
            margin-bottom: 27px;
            
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            cursor: pointer;
            i {
                display: inline-block; 
                transform: rotate(-90deg);
                transition: $tr;
            }
            &.close {
                transform: rotate(90deg)
            }
            
        }
        &-reset {
            font-family: Roboto;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 16px;
            
            text-align: center;
            display: block;
            
            padding: 10px;
            color: $blue;
            text-decoration: none;
        }
    }
    
    &__list {
        flex-wrap: wrap;
        margin-bottom: 58px;
    }
    &__item {
        border: 1px solid #EDEEF2;
        width: calc( 25% - 8px );
        margin-left: 4px;
        margin-right: 4px;
        
        padding: 30px 23px;
        
        font-family: $Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        &-img {
            max-height: 173px;
            overflow: hidden;
            
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                max-width: 100%;
                display: block;
            }
        }
        &-name {
            
            color: $blue;
            text-decoration: none;
            
            display: block;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            height: 30px;
            margin-bottom: 26px;
        }
        &-btn {
            padding: 8px 0;
            width: 100%;
            max-width: 136px;
            &:hover {
                border-color: $red;
                background: $red;
                color: #fff;
            }
        }
    }
    &__more {
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 70px;
        &-text {
            margin-bottom: 23px;
            
            font-family: Roboto;
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 16px;
        }
    }
}