.search {
    input {
        outline: none;
        &[type="text"] {
            padding: 14px;
            border: 2px solid $grey;

            font-weight: 100;
            color: $grey;

            border-radius: 35px;
        }
    }
}

.checkbox {
    display: flex;
    align-items: center;
    
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    
    margin-bottom: 10px;
    
    cursor: pointer;
    input {
        &[type="checkbox"] {
            display: none;
            & + i {
                width: 20px;
                height: 20px;
                border: 2px solid #757575;
                box-sizing: border-box;
                border-radius: 3px;
                
                display: inline-flex;
                justify-content: center;
                align-items: center;
                
                margin-right: 11px;
                
                color: $red;
            }
            &:checked {
                & + i:after {
                    font-size: 10px;
                    
                    content: "\e902";
                    font-family: 'icomoon' !important;
                    speak: none;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;

                }
            }
        }
    }
    span {
        font-weight: normal;
        display: inline-block;
        margin-left: 4px;
    }
}
