
.about {
    padding: 70px 0 130px 0; 
    @media (max-width:800px) {
        .block_head {
            text-align: center;
        }
    }
    &__advantages {
        align-items: stretch;
        margin-bottom: 50px;
        
        @media (max-width:650px) {
            flex-direction: column;   
            margin-bottom: 23px;
        }
        .block {
            &-left {
                flex-direction: column; 

                justify-content: center;
                align-items: flex-start;
                
            }
        }
        &.block-left {
            margin-bottom: 0;
            @media (max-width:800px) {
                width: 100%;
                text-align: center;
            }
        }
        &-text {
            
            
            background: $blue;
            color: #fff;
            font-style: normal;
            font-weight: 300;
            font-size: 20px;
            line-height: 23px;
            
            padding-left: 40px;
            padding-right: 90px;
            
            @media (max-width:900px) {
                font-size: 14px;
                line-height: 16px;
                padding-right: 40px;
            }
            @media (max-width:650px) {
                    align-items: center!important;
                    padding: 0;
                    background: none;
                    width: 100%;
                    order: 2;
                    
                    text-align: center;
                    color: $blue;
                    padding-bottom: 23px;
                
                    border-bottom: 1px solid #757575;
                & > * {
                    width: 100%;
                }
                
                
            }
        }
        &-img {
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            border-radius: 0px 5px 5px 0px;
            @media (max-width:650px) {
                width: 100%;   
                border-radius: 5px;
                margin-bottom: 20px;
            }
            img {
                max-width: 100%;
                display: block;
            }
            @media (max-width:800px) {
                &-r {
                    display: none;
                }
            }
        }
        h2 {
            font-style: normal;
            font-weight: normal;
            font-size: 30px;
            line-height: 35px;
            margin: 0;
            padding-bottom: 20px;
            @media (max-width:900px) {
                font-size: 20px;
                line-height: 23px;
            }
        }
    }
    &__sert {
        justify-content: space-between;
        a {
            width: 30%; 
        }
        img {
            width: 100%;
        }
        &-text {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 19px;
            
            margin-bottom: 50px;
            @media (max-width:800px) {
                text-align: center;
            }
            h2 {
                text-align: center;
                font-style: normal;
                font-weight: normal;
                font-size: 36px;
                line-height: 42px;
                margin: 0 0 43px 0;
                padding-top: 30px; 
            }
        }
    }
}
