@import "variables";
@import "mixins";
html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $Roboto;
    background: $lgrey;
}
body * {
    box-sizing: border-box;
}
.wrapper {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px; 
}
.flex {
    display: flex;
}